import React from 'react';

import SLDSSection from './SLDSSection';
import { Spinner, Checkbox, Combobox, Datepicker, Button } from '@salesforce/design-system-react';

import {baseUrl, GetParams, orgid} from '../lib/grax-data-lake';

import $ from 'jquery';
import { comboboxFilterAndLimit, getUTCTimeRangeFromUserDate} from '../lib/utils';

import GRAXSummaryChartsChart from './GRAXSummaryChartsChart';

export default class GRAXSummaryChartsSimple extends React.Component {
    constructor(props) {
        super(props);
        this.chart = React.createRef();

        this.summaryDataResults = [];
        let fromDate = new Date();
        let toDate = new Date();

        fromDate.setHours(0);
        fromDate.setMinutes(0);
        toDate.setHours(23);
        toDate.setMinutes(59);

        this.state = {
            isLoading: true,
            fromDate: fromDate,
            toDate: toDate,
            _currentOrgRecords: true,

            objectInput: '',
            objectSelection: [],
            objectOptions: [],

            chartData: [],
            chartSelected: [],
        };
    }

    componentDidMount() {
        this.loadSummaries =  function(){
            this.setState({
                isLoading: true
            });

            let fromDate = this.state.fromDate;
            fromDate = fromDate.getFullYear() + '-' + (fromDate.getMonth()+1).toString().padStart(2,0) + '-' + fromDate.getDate().toString().padStart(2,0);
            let toDate = this.state.toDate;
            toDate = toDate.getFullYear() + '-' + (toDate.getMonth()+1).toString().padStart(2,0) + '-' + toDate.getDate().toString().padStart(2,0);

            fromDate = getUTCTimeRangeFromUserDate({ date: fromDate })[0];
            toDate = getUTCTimeRangeFromUserDate({ date: toDate })[1];

            let query = {"AND": [
              {"GTE": {"summaryResults.startTime": fromDate}},
              {"LTE": {"summaryResults.startTime": toDate}}
            ]};

            if(this.state._currentOrgRecords) {
              query.AND.push({"KEQ": {"graxorgid": orgid}});
            }

            $.ajax({
                type: 'GET',

                url: baseUrl + '/grax-app/grax-data-lake' + GetParams({
                    action:'load',
                    dataIndex: 'graxgrax_backup_summaryindex',
                    _index: 'graxgrax_backup_summaryindex,graxgrax_summaryindex',
                    _select: 'summaryMap,spName,CreatedDate,summaryResults',
                    _q: JSON.stringify(query),
                    _limit: 1000,
                    _from: 0,
                    _sort: '',
                    _sortType: '',
                    _ignore_unavailable: true
                }),

                success: function(data) {
                    // IF NO data.results THEN LOAD FOR WORKING ONLY LOCALLY
                    if( data.results ){
                        this.summaryDataResults = data.results;
                    }

                    this.setState({
                        isLoading: false
                    }, function(){
                        this.loadSummaryData();
                    });
                }.bind(this),
                error: function(xhr, status, err) {
                    console.error(xhr, status, err);

                    this.setState({
                        isLoading: false,
                    });
                }.bind(this)
            });
        };

        this.loadSummaryData = function(){
            let summaryData = this.summaryDataResults;
            let summaryNames = new Set();
            let sortedNames = [];

            summaryData.forEach(function(summary){
                for( let key in summary.summaryMap ){
                    summaryNames.add( key );
                }
            });

            sortedNames = new Array(...summaryNames);

            sortedNames.sort(function(a, b){
                return  a < b?-1:a > b?1:0;
            });

            let objectOptions = [{label:"All", id:"ALL"}];
            sortedNames.forEach(function(name){
                objectOptions.push( {label: name, id: name} );
            });

            this.setState({
                isLoading: false,
                objectOptions: objectOptions,
                objectSelection: [objectOptions[0]]
            }, this.refreshChart );
        };

        this.refreshChart = function(){
            let summaryData = this.summaryDataResults;
            let selectedObjects = [];
            let chartData = [];

            if( this.state.objectSelection && this.state.objectSelection[0].id !== "ALL" ){
                selectedObjects.push( this.state.objectSelection[0].id );
            }else{
                this.state.objectOptions.forEach( function(option){
                    selectedObjects.push( option.id );
                } );
            }


            summaryData.forEach(function(summary){
                selectedObjects.forEach( function( selectedObject ){
                    if( summary.summaryMap[selectedObject] ){
                        let tempData = {
                            summaryMap: {
                                [selectedObject]: {...summary.summaryMap[selectedObject]}
                            },
                            spName: summary.spName,
                            summaryResults:{
                                ...summary.summaryMap[selectedObject]
                            }
                        };

                        chartData.push( tempData );
                    }
                });
            });

            this.setState({
                isLoading: false,
                chartData: chartData
            },function(){
                this.chart.current.drawChart();
            }.bind(this));
        }

        this.chartSelection = function(selection){
            this.setState( {
                chartSelected: selection
            } );
        };

        this.formatStartTime = function( startTime ){
            return startTime.getFullYear()+"-"+(startTime.getMonth()+1).toString().padStart(2,"0")+"-"+startTime.getDate().toString().padStart(2,"0")+" "+startTime.getHours().toString().padStart(2,"0")+":"+startTime.getMinutes().toString().padStart(2,"0")+":"+startTime.getSeconds().toString().padStart(2,"0");
        };

        this.formatDate = function( dt ){
            return dt.getMonth()+1+'/'+dt.getDate()+'/'+dt.getFullYear();
        }

        this.setState({
            isLoading: false
        });
    }

    render() {
        return (
            <div>
                {this.state.isLoading && (<Spinner size="large" variant="brand" />
				)}
                <div className="slds-grid slds-wrap">
                    <div className="slds-col slds-size_1-of-1">
                        <SLDSSection title="Filter Criteria" >
                            <div className="slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-1">

                                <dl class="slds-list_stacked">
                                    <dt class="slds-item_label slds-text-color_weak slds-truncate" title="From">
                                        <label class="slds-form-element__label" >From</label>
                                    </dt>
                                    <dd class="slds-item_detail" >
                                        <Datepicker
                                            onChange={(event, data) => {
                                                this.setState({
                                                    fromDate: data.date
                                                });
                                            }}

                                            value={ this.state.fromDate }
                                        />
                                    </dd>
                                    <dt class="slds-item_label slds-text-color_weak slds-truncate" title="To">
                                        <label class="slds-form-element__label" >To</label>
                                    </dt>
                                    <dd class="slds-item_detail" >
                                        <Datepicker
                                            onChange={(event, data) => {
                                                this.setState({
                                                    toDate: data.date
                                                });
                                            }}

                                            value={ this.state.toDate }
                                        />
                                    </dd>
                                </dl>
                            </div>
                            <div className="slds-col slds-size_1-of-1 slds-medium-size_2-of-4 slds-large-size_1-of-4 slds-m-top_small">
                                <Checkbox
                                    className="inline-block"
                                    defaultChecked={!this.state._currentOrgRecords}
                                    onChange={(e) => {
                                        this.setState({
                                            _currentOrgRecords: !e.target.checked
                                        });
                                    }}
                                    assistiveText={{
                                        label: "Show records from all environments?"
                                    }}
                                    variant="toggle"
                                    labels={{
                                        toggleDisabled: 'Current Environment',
                                        toggleEnabled: 'All connected SF environments'
                                    }}
                                />

                                <div class="slds-float_right">
                                    <Button label="Retrieve" variant="brand" onClick={() => {
                                        this.loadSummaries();
                                        this.chart.current.setDateFilter( this.state.fromDate, this.state.toDate );
                                        this.setState( {
                                            chartSelected: this.chart.current.state.chartSelection
                                        } );
                                    }} />
                                </div>
                            </div>
                        </SLDSSection>
                    </div>
                    { this.state.objectOptions && (
                    <div className="slds-col slds-size_1-of-1">
                        <SLDSSection title="Chart Options" isCollapsible="true" >

                            <div className="slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-1">
                                <Combobox
                                    events={{
                                        onChange: (event, { value }) => {
                                            this.setState( {
                                                objectInput : value
                                            }, this.refreshChart );
                                        },
                                        onRequestRemoveSelectedOption: (event, data) => {
                                            this.setState( {
                                                objectInput : '',
                                                objectSelection : data.selection
                                            } );
                                        },
                                        onSelect: (event, data) => {
                                            this.setState( {
                                                objectInput : '',
                                                objectSelection : data.selection
                                            }, this.refreshChart );
                                        },
                                    }}

                                    labels={{
                                        label: 'Salesforce Object',
                                        placeholder: '-- SELECT SALESFORCE OBJECT --',
                                    }}

                                    options={comboboxFilterAndLimit({
                                        inputValue: this.state.objectInput,
                                        limit: 1000,
                                        options: this.state.objectOptions,
                                        selection: this.state.objectSelection
                                    })}

                                    selection={this.state.objectSelection}
                                    value={this.state.objectInput}
                                    variant="readonly"
                                />
                            </div>

                            {this.chart.current &&
                            (<Combobox
                                events={{
                                    onChange: (event, { value }) => {
                                        this.chart.current.chartInput = value;
                                        this.chart.current.setState( {
                                            chartInput : value
                                        } );
                                    },
                                    onRequestRemoveSelectedOption: (event, data) => {
                                        this.chart.current.selectChart(data.selection)
                                    },
                                    onSelect: (event, data) => {
                                        if( data.selection.length > 0 ){
                                            this.chart.current.selectChart(data.selection);

                                            this.setState( {
                                                chartSelected: data.selection
                                            } );
                                        }
                                    },
                                }}

                                labels={{
                                    label: 'Chart Type',
                                    placeholder: '-- SELECT CHART TYPE --',
                                }}

                                options={comboboxFilterAndLimit({
                                    inputValue: this.chart.current.state.chartInput,
                                    limit: 1000,
                                    options: this.chart.current.state.chartOptions,
                                    selection: this.state.chartSelected?this.state.chartSelected:this.chart.current.state.chartSelection
                                })}

                                selection={this.state.chartSelected}
                                value={this.chart.current.state.chartInput}
                                variant="readonly"
                            />)}
                        </SLDSSection>
                    </div>) }
                </div>

                <GRAXSummaryChartsChart
                    ref={this.chart}
                    summaryData={this.state.chartData}
                    showAttributeFilter={false}
                    hideChartOptions={true}
                    onChartSelect={(event, selection)=>{
                        this.chartSelection( selection );
                    }}
                />

            </div>
        );
    }
}