import React from 'react';

import { Tabs, TabsPanel, Spinner } from '@salesforce/design-system-react';

// import '../public/js/jquery.sldsTree.js';

// import '../public/js/googlecharts.js';
import GRAXSummaryChartsSimple from './GRAXSummaryChartsSimple';
import GRAXSummaryChartsAdvanced from './GRAXSummaryChartsAdvanced';

export default class GRAXSummaryCharts extends React.Component {
    constructor(props) {
        super(props);
		
		this.state = {
            isLoading: true
        };
    }

    componentDidMount() {
		this.setState({
			isLoading: false
		});
    }

    render() {
        if(this.state.isLoading) {
            return (
                <Spinner size="large" variant="brand" />
            );
        }

        return (
			<div style={{ minHeight: 1000 }} >
				<Tabs variant={this.props.variant} id="tabs-simple-advanced" >
					<TabsPanel label="Simple">
						<GRAXSummaryChartsSimple />
					</TabsPanel>
					<TabsPanel label="Advanced" style={{padding:0}} >
						<GRAXSummaryChartsAdvanced />
					</TabsPanel>
				</Tabs>
			</div>
        )
    }
}
