/* eslint-disable no-undef */
/* global google */
import React from 'react';


import SLDSSection from './SLDSSection';
import { Spinner, Checkbox, Combobox } from '@salesforce/design-system-react';

import {comboboxFilterAndLimit} from '../lib/utils';


export default class GRAXSummaryChartsChart extends React.Component {
    constructor(props) {
        super(props);

        google.charts.load('current', {'packages':['corechart']}); // Move to componentDidMount
        google.charts.setOnLoadCallback( this.drawChart ); // Move to componentDidMount

        let fromDate = this.props.fromDate?this.props.fromDate:new Date();
        let toDate = this.props.toDate?this.props.toDate:new Date();

        fromDate.setHours(0);
        fromDate.setMinutes(0);
        toDate.setHours(23);
        toDate.setMinutes(59);

        this.state = {
            isLoading: true,
            summaryData: this.props.summaryData,
            defaultChartSettings: {'width':"100%", 'height':600, "chartArea":{ width: "70%", left: "5%" } },
            showChartAttr: {
                showRecords: true,
                showInserted: true,
                showUpdated: true,
                showArchived: true
            },

            chartFromDate: fromDate,
            chartToDate: toDate,
            showAttributeFilter: true,
            hideChartOptions: false
        };
    }

    static getDerivedStateFromProps(nextProps, prevState){
        return nextProps;
    }

    componentDidMount() {
        this.chartObjectTopTen = function(){
            let summaryData = this.state.summaryData;
            let otherSum = 0;
            let objIndexes = {};
            let chartDataIndexes = [];
            let chartData = [];

            let counter = 0;
            let nameSet = new Set();
            let sortedNames;

            summaryData.forEach(function( summary ){
                for( let key in summary.summaryMap ){
                    nameSet.add(key);
                };
            });

            sortedNames = new Array(...nameSet);

            sortedNames.sort(function(a, b){
                return  a > b?-1:a < b?1:0;
            });

            sortedNames.forEach(function(item){
                objIndexes[item] = counter;
                counter++;
            });

            counter = 0;

            summaryData.forEach(function( summary ){
                for( let key in summary.summaryMap ){
                    let obj = summary.summaryMap[key];
                    let smyIndex = chartDataIndexes[key];

                    let n = (obj.records?obj.records:0);


                    if( !smyIndex && smyIndex !== 0 ){
                        smyIndex = counter;
                        chartDataIndexes[key] = counter;
                        chartData.push( [ key + ' ['+n+']', n] );
                        counter++;
                    }else{
                        let tempN = chartData[smyIndex][1] + n;

                        chartData[smyIndex] = [ key + ' ['+tempN+']', tempN];
                    }

                    otherSum += n;
                };
            });

            chartData.sort(function(a, b){
                return - a[1] + b[1];
            });

            let top10BackedUpObjects = [["Object", "Backed Up"]];

            for( let i=0; i<10 && i<chartData.length; i++){
                let rec = chartData[ i ];
                top10BackedUpObjects.push( rec );
                otherSum -= rec[1];
            }

            if( otherSum > 0 ){
                top10BackedUpObjects.push( ["Others [" + otherSum + "]", otherSum] );
            }

            let data = google.visualization.arrayToDataTable(top10BackedUpObjects);

            // Optional; add a title and set the width and height of the chart
            let options = JSON.parse(JSON.stringify(this.state.defaultChartSettings));
            options.sliceVisibilityThreshold = 0;

            // Display the chart inside the <div> element with id="piechart"
            return [ google.visualization.PieChart, data, options ];
        }.bind(this);

        this.chartObjectByDate = function(){
            let summaryData = this.state.summaryData;
            let chartData = [];
            let chartDataIndexes = [];
            let objIndexes = {};

            let data = new google.visualization.DataTable();
            data.addColumn('date', 'Start Time');

            let counter = 0;
            let nameSet = new Set();
            let sortedNames;

            summaryData.forEach(function( summary ){
                for( let key in summary.summaryMap ){
                    nameSet.add(key);
                };
            });

            sortedNames = new Array(...nameSet);

            sortedNames.sort(function(a, b){
                return  a > b?-1:a < b?1:0;
            });

            sortedNames.forEach(function(item){
                objIndexes[item] = counter;
                counter++;
            });

            counter = 0;
            let fromDateAxis;
            let toDateAxis;

            let objectsTotalValue = {};
            /*
            [
                [
                    {v: date, f: label},
                    val1,
                    val2,
                    val3
                ],
                [...]
            ]
            */
            summaryData.forEach(function(summary){
                for( let key in summary.summaryMap ){
                    let obj = summary.summaryMap[key];

                    if(!obj.startTime) {
                      continue;
                    }

                    let n = (obj.records?obj.records:0);
                    let smyDate = new Date(obj.startTime);
                    let smyDateFormated = this.formatStartTime(smyDate);
                    let smyIndex = chartDataIndexes[smyDate];


                    if( !fromDateAxis || fromDateAxis >= smyDate ){
                        fromDateAxis = new Date(smyDate);
                        fromDateAxis.setHours(fromDateAxis.getHours()-1);
                    }

                    if( !toDateAxis || toDateAxis <= smyDate ){
                        toDateAxis = new Date(smyDate);
                        toDateAxis.setHours(toDateAxis.getHours()+1);
                    }

                    if( !smyIndex && smyIndex !== 0 ){
                        smyIndex = counter;
                        chartDataIndexes[smyDate] = counter;
                        chartData.push( new Array( sortedNames.length + 1 ).fill(undefined) );
                        counter++;
                    }

                    chartData[smyIndex][0] = {v: smyDate, f: smyDateFormated };
                    if( !chartData[smyIndex][objIndexes[key]+1] ){
                        chartData[smyIndex][objIndexes[key]+1] = 0;
                    }
                    chartData[smyIndex][objIndexes[key]+1] += n;
                    if( !objectsTotalValue[key] ){
                        objectsTotalValue[key] = 0;
                    }
                    objectsTotalValue[key] += n
                };
            }.bind(this));

            if( chartData.length === 0 ) return null;

            for( let key in objIndexes ){
                data.addColumn('number', key + " [" + objectsTotalValue[key] + "]");
            };

            chartData.sort(function(a,b){
                return a[0].v > b[0].v?1:a[0].v < b[0].v?-1:0;
            });

            data.addRows( chartData );

            // Optional; add a title and set the width and height of the chart
            let options = JSON.parse(JSON.stringify(this.state.defaultChartSettings));
            options.interpolateNulls = true;
            options.pointSize = 5;
            options.explorer = {};
            options.hAxis = {
                title: 'Start Time',
                format: 'YYYY-dd-MM\nhh:mm:ss',
                viewWindow: {
                    min: fromDateAxis,
                    max: toDateAxis
                },
                viewWindowMode: 'maximized'
            };
            options.vAxis = {
                title: 'Backed up objects'
            };

            // Display the chart inside the <div> element
            return [ google.visualization.LineChart, data, options ];
        }.bind(this);

        this.chartSummaryTopTen = function(){
            let summaryData = this.state.summaryData;
            let otherSum = 0;
            let summaryIndexes = {};
            let chartDataIndexes = [];
            let chartData = [];

            let counter = 0;
            let nameSet = new Set();
            let sortedNames;

            summaryData.forEach(function( summary ){
                nameSet.add( summary.spName );
            });

            sortedNames = new Array(...nameSet);

            sortedNames.sort(function(a, b){
                return  a > b?-1:a < b?1:0;
            });

            sortedNames.forEach(function(item){
                summaryIndexes[item] = counter;
                counter++;
            });

            counter = 0;

            summaryData.forEach(function( summary ){
                let smyIndex = chartDataIndexes[summary.spName];
                let n = (summary.summaryResults.records?summary.summaryResults.records:0);

                if( !smyIndex && smyIndex !== 0 ){
                    chartData.push( [ summary.spName + ' ['+n+']', n] );
                }else{
                    let tempN = chartData[smyIndex][1] + n;

                    chartData[smyIndex] = [ summary.spName + ' ['+tempN+']', tempN];
                }
                otherSum += n;
            });

            chartData.sort(function(a, b){
                return - a[1] + b[1];
            });

            let top10BackedUpSummaries = [["Summary", "Backed Up"]];

            for( let i=0; i<10 && i<chartData.length; i++){
                let rec = chartData[ i ];
                top10BackedUpSummaries.push( rec );
                otherSum -= rec[1];
            }

            if( otherSum > 0 ){
                top10BackedUpSummaries.push( ["Others [" + otherSum + "]", otherSum] );
            }

            let data = google.visualization.arrayToDataTable(top10BackedUpSummaries);

            // Optional; add a title and set the width and height of the chart
            let options = JSON.parse(JSON.stringify(this.state.defaultChartSettings));
            options.sliceVisibilityThreshold = 0;

            // Display the chart inside the <div> element with id="piechart"
            return [ google.visualization.PieChart, data, options ];
        }.bind(this);

        this.chartSummaryByDate = function(){
            let summaryData = this.state.summaryData;
            let chartData = [];
            let chartDataIndexes = [];
            let objIndexes = {};

            let data = new google.visualization.DataTable();
            data.addColumn('date', 'Start Time');

            let counter = 0;
            let nameSet = new Set();
            let sortedNames;

            summaryData.forEach(function( summary ){
                nameSet.add( summary.spName );
            });

            sortedNames = new Array(...nameSet);

            sortedNames.sort(function(a, b){
                return  a > b?-1:a < b?1:0;
            });

            sortedNames.forEach(function(item){
                objIndexes[item] = counter;
                counter++;
            });

            counter = 0;
            let fromDateAxis;
            let toDateAxis;

            let objectsTotalValue = {};
            /*
            [
                [
                    {v: date, f: label},
                    val1,
                    val2,
                    val3
                ],
                [...]
            ]
            */
            summaryData.forEach(function(summary){
                let sumRes = summary.summaryResults;

                if(!sumRes.startTime) {
                  return;
                }

                let n = (sumRes.records?sumRes.records:0);
                let smyDate = new Date(sumRes.startTime);
                let smyDateFormated = this.formatStartTime(smyDate);
                let smyIndex = chartDataIndexes[smyDate];

                if( !fromDateAxis || fromDateAxis >= smyDate ){
                    fromDateAxis = new Date(smyDate);
                    fromDateAxis.setHours(fromDateAxis.getHours()-1);
                }

                if( !toDateAxis || toDateAxis <= smyDate ){
                    toDateAxis = new Date(smyDate);
                    toDateAxis.setHours(toDateAxis.getHours()+1);
                }

                if( !smyIndex && smyIndex !== 0 ){
                    smyIndex = counter;
                    chartDataIndexes[smyDate] = counter;
                    chartData.push( new Array( sortedNames.length + 1 ).fill(undefined) );
                    counter++;
                }

                chartData[smyIndex][0] = {v: smyDate, f: smyDateFormated };
                if( !chartData[smyIndex][objIndexes[summary.spName]+1] ){
                    chartData[smyIndex][objIndexes[summary.spName]+1] = 0;
                }
                chartData[smyIndex][objIndexes[summary.spName]+1] += n;
                if( !objectsTotalValue[summary.spName] ){
                    objectsTotalValue[summary.spName] = 0;
                }
                objectsTotalValue[summary.spName] += n
            }.bind(this));

            if( chartData.length === 0 ) return null;

            for( let key in objIndexes ){
                data.addColumn('number', key + " [" + objectsTotalValue[key] + "]");
            };

            chartData.sort(function(a,b){
                return a[0].v > b[0].v?1:a[0].v < b[0].v?-1:0;
            });

            data.addRows( chartData );

            // Optional; add a title and set the width and height of the chart
            let options = JSON.parse(JSON.stringify(this.state.defaultChartSettings));
            options.interpolateNulls = true;
            options.explorer = {};
            options.hAxis = {
                title: 'Start Time',
                format: 'YYYY-dd-MM\nhh:mm:ss',
                viewWindow: {
                    min: fromDateAxis,
                    max: toDateAxis
                },
                viewWindowMode: 'maximized'
            };
            options.vAxis = {
                title: 'Backed up objects'
            };

            // Display the chart inside the <div> element
            return [ google.visualization.LineChart, data, options ];
        }.bind(this);

        this.chartAttributeProgressByDate = function(){
            let summaryData = this.state.summaryData;
            let chartData = [];
            let chartDataIndexes = [];
            let attIndexes = {};

            let data = new google.visualization.DataTable();
            data.addColumn('date', 'Start Time');

            let counter = 0;
            let sortedNames = [];
            let attrNames = [];

            if( this.state.showChartAttr.showRecords ){ attrNames.push( "records" ); }
            if( this.state.showChartAttr.showInserted ){ attrNames.push( "inserted" ); }
            if( this.state.showChartAttr.showUpdated ){ attrNames.push( "updated" ); }
            if( this.state.showChartAttr.showArchived ){ attrNames.push( "archived" ); }

            attrNames.forEach(function(attrName){
                sortedNames.push( attrName );
            });

            sortedNames.sort(function(a, b){
                return  a < b?-1:a > b?1:0;
            });

            sortedNames.forEach(function(item){
                attIndexes[item] = counter;
                counter++;
            });

            counter = 0;
            let fromDateAxis;
            let toDateAxis;
            let summarysTotalValue = {};
            let selectedSummaries = new Set();

            summaryData.forEach(function(summary){
                let attr = {};
                let summaryMap = summary.summaryMap;
                let containsSelectedObjects = false;

                for( let key in summaryMap ){
                    let obj = summaryMap[key];
                    if( obj ){
                        attrNames.forEach( function(attrName){
                            attr[attrName] = attr[attrName]?attr[attrName]:0;
                            attr[attrName] += (obj[attrName]?obj[attrName]:0);
                        } );
                        containsSelectedObjects = true;
                    }
                };

                if( containsSelectedObjects && summary.summaryResults.startTime ){
                    let startTime = new Date(summary.summaryResults.startTime);
                    startTime.setSeconds(0);

                    let smyDate = startTime;
                    let smyDateFormated = startTime.getFullYear()+"-"+(startTime.getMonth()+1).toString().padStart(2,"0")+"-"+startTime.getDate().toString().padStart(2,"0")+" "+startTime.getHours().toString().padStart(2,"0")+":"+startTime.getMinutes().toString().padStart(2,"0")+":"+startTime.getSeconds().toString().padStart(2,"0");
                    let smyIndex = chartDataIndexes[smyDate];

                    if( !fromDateAxis || fromDateAxis >= smyDate ){
                        fromDateAxis = new Date(smyDate);
                        fromDateAxis.setHours(fromDateAxis.getHours()-1);
                    }

                    if( !toDateAxis || toDateAxis <= smyDate ){
                        toDateAxis = new Date(smyDate);
                        toDateAxis.setHours(toDateAxis.getHours()+1);
                    }

                    if( !smyIndex && smyIndex !== 0 ){
                        smyIndex = counter;
                        chartDataIndexes[smyDate] = counter;
                        chartData.push( new Array( attrNames.length + 1 ).fill(undefined) );
                        counter++;
                    }

                    chartData[smyIndex][0] = {v: smyDate, f: smyDateFormated + "\n" + summary.spName };

                    attrNames.forEach(function(attrName){
                        if( !chartData[smyIndex][attIndexes[attrName]+1] ){
                            chartData[smyIndex][attIndexes[attrName]+1] = 0;
                        }
                        chartData[smyIndex][attIndexes[attrName]+1] += attr[attrName];

                        if( !summarysTotalValue[attrName] ){
                            summarysTotalValue[attrName] = 0;
                        }
                        summarysTotalValue[attrName] += attr[attrName];
                    });

                    selectedSummaries.add( summary.spName );
                }
            });

            if( chartData.length === 0 ) return null;

            for( let key in attIndexes ){
                if( summarysTotalValue[key] !== undefined ){
                    data.addColumn('number', key + " [" + summarysTotalValue[key] + "]");
                }
            };

            chartData.sort(function(a,b){
                return a[0] > b[0]?1:a[0] < b[0]?-1:0;
            });

            data.addRows( chartData );

            // Optional; add a title and set the width and height of the chart
            let options = JSON.parse(JSON.stringify(this.state.defaultChartSettings));
            options.interpolateNulls = true;
            options.isStacked = true;
            options.explorer = {};
            options.hAxis = {
                title: 'Start Date',
                format: 'YYYY-dd-MM\n hh:mm:ss',
                viewWindowMode: 'pretty',
                viewWindow: {
                    min: fromDateAxis,
                    max: toDateAxis
                }
            };
            options.vAxis = {
                title: 'Attributes'
            };

            // Display the chart inside the <div> element
            return [ google.visualization.ColumnChart, data, options ];
        }.bind(this);

        this.drawChart = function(){
            let ret = this.state.chartSelection[0].call();

            this.setState({
                isLoading: false,
                isBlank: ret == null
            }, function(){
                if( ret ){
                    var chart = new ret[0]( this.chartEl );
                    chart.draw(ret[1],ret[2]);
                }
            });
        }.bind(this);

        this.selectChart = function( selection ){
            this.setState( {
                chartInput : '',
                chartSelection : selection
            }, this.drawChart );
        }.bind(this);

        this.setDateFilter = function( fromDate, toDate ){
            this.setState({
                chartFromDate: fromDate,
                chartToDate: toDate
            });
        }.bind(this);

        this.formatStartTime = function( startTime ){
            return startTime.getFullYear()+"-"+(startTime.getMonth()+1).toString().padStart(2,"0")+"-"+startTime.getDate().toString().padStart(2,"0")+" "+startTime.getHours().toString().padStart(2,"0")+":"+startTime.getMinutes().toString().padStart(2,"0")+":"+startTime.getSeconds().toString().padStart(2,"0");
        };

        this.formatDate = function( dt ){
            return dt.getMonth()+1+'/'+dt.getDate()+'/'+dt.getFullYear();
        }

        let chartOptions = [{label: "Object Top Ten", id: "chartObjectTopTen", call : this.chartObjectTopTen },
                    {label: "Objects By Date", id: "chartObjectByDate", call : this.chartObjectByDate},
                    {label: "Top Ten Processes", id: "chartSummaryTopTen", call : this.chartSummaryTopTen},
                    {label: "Processes By Date", id: "chartSummaryByDate", call : this.chartSummaryByDate},
                    {label: "Results by Date", id: "chartAttributeProgressByDate", call : this.chartAttributeProgressByDate}
        ];

        this.setState({
            isLoading: false,
            chartOptions: chartOptions,
            chartSelection: [chartOptions[0]]
        });
    }

    render() {
        if(this.state.isLoading) {
            return (
                <Spinner size="large" variant="brand" />
            );
        }

        if(this.state.isBlank) return null;

        return (
            <div>
                <div>
                    { !this.state.hideChartOptions &&
                    (<div className="slds-grid slds-wrap">
                        <div className="slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-1">
                            <SLDSSection title="Chart Options" isCollapsible="true" >
                                <div className="slds-grid slds-wrap">

                                    <div className={(this.state.chartSelection[0].id === 'chartAttributeProgressByDate' && this.state.showAttributeFilter)?"slds-col slds-size_1-of-2 slds-medium-size_1-of-2 slds-large-size_1-of-2":"slds-col slds-size_1-of-1 slds-medium-size_1-of-1 slds-large-size_1-of-1"}>
                                        <Combobox
                                            events={{
                                                onChange: (event, { value }) => {
                                                    this.chartInput = value;
                                                    this.setState( {
                                                        chartInput : value
                                                    }, this.drawChart );
                                                },
                                                onRequestRemoveSelectedOption: (event, data) => {
                                                    this.setState( {
                                                        chartInput : '',
                                                        chartSelection : data.selection
                                                    } );
                                                },
                                                onSelect: (event, data) => {
                                                    this.selectChart( data.selection );

                                                    if (this.props.onChartSelect) {
                                                        this.props.onChartSelect(event, data.selection);
                                                    }
                                                },
                                            }}

                                            labels={{
                                                label: 'Chart Type',
                                                placeholder: '-- SELECT CHART TYPE --',
                                            }}

                                            options={comboboxFilterAndLimit({
                                                inputValue: this.state.chartInput,
                                                limit: 1000,
                                                options: this.state.chartOptions,
                                                selection: this.state.chartSelection
                                            })}

                                            selection={this.state.chartSelection}
                                            value={this.state.chartInput}
                                            variant="readonly"
                                        />
                                    </div>

                                    {this.state.chartSelection[0].id === 'chartAttributeProgressByDate' && this.state.showAttributeFilter && (
                                    <div className="slds-col slds-size_1-of-2 slds-medium-size_1-of-2 slds-large-size_1-of-2">
                                        <SLDSSection title="Display Values" className="slds-p-left_small slds-m-top_none" >
                                            <div className="slds-grid slds-wrap">
                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-2 slds-medium-size_1-of-4 slds-large-size_1-of-4 slds-x-large-size_1-of-4">
                                                    <Checkbox
                                                        className="inline-block"
                                                        defaultChecked={this.state.showChartAttr.showRecords}
                                                        onChange={(isChecked) => {
                                                            let showChartAttr = this.state.showChartAttr;
                                                            showChartAttr.showRecords = isChecked;
                                                            this.setState({
                                                                showChartAttr: showChartAttr,
                                                                isLoading : false
                                                            }, function(){ this.drawChart();} );
                                                        }}
                                                        assistiveText={{
                                                            label: "Remove empty?"
                                                        }}
                                                        variant="toggle"
                                                        labels={{
                                                            toggleDisabled: 'Records',
                                                            toggleEnabled: 'Records'
                                                        }}
                                                    />
                                                </div>
                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-2 slds-medium-size_1-of-4 slds-large-size_1-of-4 slds-x-large-size_1-of-4">
                                                    <Checkbox
                                                        className="inline-block"
                                                        defaultChecked={this.state.showChartAttr.showInserted}
                                                        onChange={(isChecked) => {
                                                            let showChartAttr = this.state.showChartAttr;
                                                            showChartAttr.showInserted = isChecked;
                                                            this.setState({
                                                                showChartAttr: showChartAttr,
                                                                isLoading : false
                                                            }, function(){ this.drawChart();} );
                                                        }}
                                                        assistiveText={{
                                                            label: "Remove empty?"
                                                        }}
                                                        variant="toggle"
                                                        labels={{
                                                            toggleDisabled: 'Inserted',
                                                            toggleEnabled: 'Inserted'
                                                        }}
                                                    />
                                                </div>
                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-2 slds-medium-size_1-of-4 slds-large-size_1-of-4 slds-x-large-size_1-of-4">
                                                    <Checkbox
                                                        className="inline-block"
                                                        defaultChecked={this.state.showChartAttr.showUpdated}
                                                        onChange={(isChecked) => {
                                                            let showChartAttr = this.state.showChartAttr;
                                                            showChartAttr.showUpdated = isChecked;
                                                            this.setState({
                                                                showChartAttr: showChartAttr,
                                                                isLoading : false
                                                            }, function(){ this.drawChart();} );
                                                        }}
                                                        assistiveText={{
                                                            label: "Remove empty?"
                                                        }}
                                                        variant="toggle"
                                                        labels={{
                                                            toggleDisabled: 'Updated',
                                                            toggleEnabled: 'Updated'
                                                        }}
                                                    />
                                                </div>
                                                <div className="slds-align_absolute-center slds-m-bottom_small slds-col slds-size_1-of-2 slds-medium-size_1-of-4 slds-large-size_1-of-4 slds-x-large-size_1-of-4">
                                                    <Checkbox
                                                        className="inline-block"
                                                        defaultChecked={this.state.showChartAttr.showArchived}
                                                        onChange={(isChecked) => {
                                                            let showChartAttr = this.state.showChartAttr;
                                                            showChartAttr.showArchived = isChecked;
                                                            this.setState({
                                                                showChartAttr: showChartAttr,
                                                                isLoading : false
                                                            }, function(){ this.drawChart();} );
                                                        }}
                                                        assistiveText={{
                                                            label: "Remove empty?"
                                                        }}
                                                        variant="toggle"
                                                        labels={{
                                                            toggleDisabled: 'Archived',
                                                            toggleEnabled: 'Archived'
                                                        }}
                                                    />
                                                </div>
                                            </div>
                                        </SLDSSection>
                                    </div>
                                    )}
                                </div>
                            </SLDSSection>
                        </div>
                    </div>)}

                    <div  ref={chartEl => this.chartEl = chartEl} />
                </div>
            </div>
        )
    }
}